.comment_box {
  position: relative;
  z-index: 999;
  background-color: #fff;
}
.comment_box .comment_title {
    font-size: 22px;
    margin: 40px 0 10px;
    border-bottom: 1px solid #ccc;
    padding: 5px 0;
    color: #462c2c;
}
.comment_box .comment-name {
    color: #61b400;
    font-size: 18px;
}
.comment_box .comment-time {
    color: #888;
    margin-left: 8px;
    font-size: 12px;
}
.comment_box .comment-content {
    margin: 10px 6px;
}
.comment_box .emoji-picker-container {
    width: 100% !important;
}
.comment_box .emoji-vue-textarea {
    resize: none;
    overflow-y: none;
    outline: none;
    font-size: 14px;
    padding: 10px;
    border: none;
    border-radius: 6px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100% !important;
    height: 100px !important;
    margin-top: 10px;
    -webkit-box-shadow: 0 0 8px rgba(0, 0, 0, 0.4);
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.4);
    margin-bottom: 10px;
}
.comment_box .block {
    margin-top: 20px;
}
.comment_box button {
    float: right;
}
.comment_box .clearfix {
    clear: both;
    overflow: hidden;
}
.comment_box .reply {
    cursor: pointer;
    margin-left: 6px;
    color: #4183c4;
}
.comment_box .reply:hover {
    color: #BE4F8D;
}
.comment_box .card {
    margin-top: 8px;
}
.comment_box .card .el-card {
      border: none;
}
.comment_box .reply-second {
    font-size: 13px;
}
.comment_box .reply-item {
    border-bottom: 1px dashed #ddd;
    padding-bottom: 6px;
}


